.footer {
    margin-top: 30px;
    background-color: #1e2329;
    padding: 20px 0;
    text-align: center;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-row {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.footer-column {
    flex: 1;
    margin: 0 10px;
    max-width: 300px;
    /* Opcional: Establece un ancho máximo para las columnas */
}

.footer-column h3 {
    color: aliceblue;
    font-size: 16px;
    margin-bottom: 10px;
}

.footer-column p {
    color: #95a4b8;
    font-size: 14px;
    margin-bottom: 5px;
}

.social-media-icons {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.social-media-icons a {
    display: inline-block;
    margin: 0 5px;
}

.footer-text {
    color: #888;
    font-size: 14px;
}

/* Estilos para pantallas más pequeñas */
@media (max-width: 768px) {
    .footer-row {
        flex-wrap: wrap;
    }

    .footer-column {
        flex: 1 1 100%;
        max-width: none;
        margin-bottom: 20px;
    }
}